import * as React from 'react'
import { useState } from 'react'
import Layout from '../components/layout'

const inlineBlock = {
  display: 'inline-block'
}

const SearchPage = () => {
  const [value, setValue] = useState('')
  return (
    <Layout home={false}>
      <h1>Search</h1>
      <div>
        <form method="GET" action="https://www.google.com/search" style={inlineBlock}>
          <input name="term" value={value} onChange={(e) => setValue(e.target.value)} type="search" placeholder="search here..." />
          <input value={'site:marchenko1985.github.io ' + value} type="hidden" name="q" />
          <input type="submit" value="search" />
        </form>
      </div>
    </Layout>
  )
}

export const Head = () => <title>Search</title>

export default SearchPage
